import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import AC from "../../components/air-conditioning-service"
import GetInTouch from "../../components/forms/get-in-touch"
import { graphql } from "gatsby"

export const query = graphql`
  query {
    wpHero(id: { eq: "cG9zdDozMTI=" }) {
      id
      title
      heroFields {
        en {
          headline
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
        es {
          headline
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
      }
    }
  }
`

export default function ACPage({ data }) {
  return (
    <Layout>
      <SEO title="Air Conditioning Repair and Installation Experts" />
      <AC data={data} />
      <GetInTouch
        locationName="Air Rey A/C &amp; Heating"
        // address="5504 Bandera Rd. Suite 202"
        city="Serving Austin, Houston, Dallas, &amp; San Antonio"
        state=""
        zip=""
        phone="469-420-5155"
        email="sales@airreydfw.com"
      />
    </Layout>
  )
}
